'use strict';

const toDoForm = document.querySelector(".js-toDoForm"),
    toDoInput = toDoForm.querySelector('input'),
    toDoList = document.querySelector(".js-toDoList");

const TODOS_LS = 'toDos';


let toDos = [];

function deletedToDo(event) {
    // target = Î≤ÑÌäºÏùò ÏúÑÏπòÎ•º ÏïÑÎäîÍ≤É parentNode = Ìï¥Îãπ Î≤ÑÌäºÏùò Î∂ÄÎ™®
    const btn = event.target;
    const li = btn.parentNode;
    toDoList.removeChild(li);
    const cleanToDos = toDos.filter(function (toDo) {
        return toDo.id !== parseInt(li.id);
    });
    toDos = cleanToDos;
    saveToDos();
}

function saveToDos() {
    // localStorageÎäî value Í∞íÏùÑ stringÏúºÎ°úÎßå Ï†ÄÏû•Í∞ÄÎä•
    // JSON.stringifyÎ•º ÌÜµÌï¥ objÎ•º stringÏúºÎ°ú Î≥ÄÌôò
    localStorage.setItem(TODOS_LS, JSON.stringify(toDos));
}

function paintToDo(text) {
    const li = document.createElement("li");
    const delBtn = document.createElement("button");
    const span = document.createElement("span");
    const newId = toDos.length + 1;

    delBtn.innerText = "‚ùå";
    delBtn.addEventListener("click", deletedToDo);
    span.innerText = text;

    li.appendChild(delBtn);
    li.appendChild(span);
    toDoList.appendChild(li);

    li.id = newId;
    const toDosObj = {
        text: text,
        id: newId
    };
    // push Î•º ÌïòÍ≥† ÎÇúÎí§ saveToDosÎ•º Î∂àÎü¨ÏôÄÏïº Ï†ÄÏû•Í∞ÄÎä•
    toDos.push(toDosObj);
    saveToDos();
}

function handleSubmit(event) {
    event.preventDefault();
    const currentValue = toDoInput.value;
    paintToDo(currentValue);
    toDoInput.value = "";
}

function loadToDos() {
    const loadedToDos = localStorage.getItem(TODOS_LS);
    if (loadedToDos !== null) {
        // JSON.parse = localStorageÏóê ÏûàÎäî value Í∞í stringÏùÑ JSÎ°ú ÏÇ¨Ïö©Ìï† Ïàò ÏûàÍ≤å Î∞îÍøîÏ£ºÎäî ÏûëÏóÖ
        const parsedToDos = JSON.parse(loadedToDos);

        // forEach = array ÏïàÏóê Ìï®ÏàòÎ•º Í∞ÅÍ∞Å Ïã§Ìñâ
        parsedToDos.forEach(function (toDo) {
            paintToDo(toDo.text);
        });
    }
}

function init() {
    loadToDos();
    toDoForm.addEventListener('submit', handleSubmit);
}
init();